import { BModal } from 'bootstrap-vue/src/components/modal';
import Vue from 'vue';
import { sendJsonRequest } from '../../services/utility-functions/send-json-request';
import RoleInfo from '../Mods/RoleInfo.vue';
import { Maps, Location, Challenges } from './types.js';
const dataURL =
    'https://raw.githubusercontent.com/TheFinalSkill/AmongUsChallenges/main/challengeList.json';
const mapDataURL =
    'https://raw.githubusercontent.com/TheFinalSkill/AmongUsChallenges/main/maps.json';

const mapLayers = [
    { text: 'Vents', value: 'vents' },
    { text: 'Common Tasks', value: 'commonTasks' },
    { text: 'Long Tasks', value: 'longTasks' },
    { text: 'Short Tasks', value: 'shortTasks' },
    { text: 'Sabotages', value: 'sabotages' },
    { text: 'Cams', value: 'cams' },
    { text: 'Wires Order', value: 'wires' },
    { text: 'Names', value: 'names' },
];

export default Vue.extend({
    name: 'AmongUs',
    components: { RoleInfo },
    data() {
        const selectedMapLayers: Partial<typeof mapLayers> = [];

        return {
            generalChallenges: new SelectableList<string>(
                'selectedGeneralChallenges'
            ),
            crewmateChallenges: new SelectableList<string>(
                'selectedCrewmateChallenges'
            ),
            impostorChallenges: new SelectableList<string>(
                'selectedImpostorChallenges'
            ),
            gameChallenges: new SelectableList<string>(
                'selectedGameChallenges'
            ),
            challengeList: new SelectableList<string>(''),
            mapName: '',
            mapWireOrder: '',
            mapIconHeight: 70,
            selectedMapLayers,
            showLayeredMap: true,
            mapHeight: 600,
            mapLayers,
            challengeType: '',
            challengeListModalColor: 'dark',
            betaMode: false,
            showTasks: false,
            mapData: {} as Maps,
            locations: [] as Location[],
        };
    },
    methods: {
        getData: async function () {
            const amongUsChallenges = await sendJsonRequest<Challenges>(
                dataURL
            );
            const mapData = await sendJsonRequest<Maps>(mapDataURL);
            this.mapData = mapData;
            this.generalChallenges.allItems =
                amongUsChallenges.generalChallenges;
            this.crewmateChallenges.allItems =
                amongUsChallenges.crewmateChallenges;
            this.impostorChallenges.allItems =
                amongUsChallenges.impostorChallenges;
            this.gameChallenges.allItems = amongUsChallenges.gameChallenges;
            this.loadLocalStorage();
            this.generateNewChallenges();
        },
        loadLocalStorage() {
            // TODO ANPASSEN:
            // General:
            this.loadLocalSorageItem(this.generalChallenges);
            // Crewmate:
            this.loadLocalSorageItem(this.crewmateChallenges);
            // Impostor:
            this.loadLocalSorageItem(this.impostorChallenges);
            // Game:
            this.loadLocalSorageItem(this.gameChallenges);
            // MapLayers:
            this.loadMapLayers();
            // Beta-Mode:
            this.checkBetaStatus();
        },
        loadLocalSorageItem<T>(item: SelectableList<string>) {
            // TODO
            const localSelection = localStorage.getItem(item.selectedKey);
            if (!localSelection) {
                item.selectedItems = item.allItems;
                return;
            }
            try {
                item.selectedItems = JSON.parse(localSelection);
            } catch (e) {
                localStorage.removeItem(item.selectedKey);
            }
        },
        getMapImagePath: function (map, layer) {
            const path =
                'https://raw.githubusercontent.com/TheFinalSkill/AmongUsChallenges/main/Maps/' +
                map +
                '/' +
                layer +
                '.png';
            return path;
        },
        newTab: function (link: string) {
            window.open(link, '_blank');
        },
        scrollToTop() {
            window.scrollTo(0, 0);
        },
        generateNewChallenges() {
            this.generateChallenge(this.generalChallenges);
            this.generateChallenge(this.crewmateChallenges);
            this.generateChallenge(this.impostorChallenges);
            this.$bvToast.toast('Neue Challenges wurden generiert.', {
                title: 'Among Us Challenges',
                variant: 'success',
                autoHideDelay: 500,
                solid: true,
            });
        },
        generateChallenge(challenges: SelectableList<string>) {
            if (challenges.selectedItems.length > 0) {
                const newChallenge =
                    challenges.selectedItems[
                        Math.floor(
                            Math.random() * challenges.selectedItems.length
                        )
                    ];
                Vue.set(challenges, 'currentItem', newChallenge);
            } else {
                const errorMsg = 'Fehler: Keine Challenges ausgewählt!';
                Vue.set(challenges, 'currentItem', errorMsg);
            }
        },
        clearLocalStorage() {
            localStorage.clear();
        },
        generateNewGameChallenge() {
            this.generateChallenge(this.gameChallenges);
        },
        getModal(modalName: string) {
            return this.$refs[modalName] as BModal;
        },
        hideModal(modalName: string) {
            this.getModal(modalName).hide();
        },
        checkBetaStatus() {
            const item = localStorage.getItem('BETA-Status');
            if (item) {
                try {
                    this.betaMode = item === 'true';
                } catch (e) {
                    localStorage.removeItem('BETA-Status');
                }
            }
        },
        saveChallengeModal() {
            switch (this.challengeType) {
                case 'General':
                    this.saveChallenge(this.generalChallenges);
                    break;
                case 'Crewmate':
                    this.saveChallenge(this.crewmateChallenges);
                    break;
                case 'Impostor':
                    this.saveChallenge(this.impostorChallenges);
                    break;
                case 'Lobby':
                    this.saveChallenge(this.gameChallenges);
                    break;
            }
            this.getModal('challengeListModal').hide();
            localStorage.setItem('Feature-Version', '1.0.0');
        },
        saveChallenge(challenges: SelectableList<string>) {
            challenges.selectedItems = this.challengeList.selectedItems;
            // const parsed = JSON.stringify(this.challengeList.selectedItems)
            localStorage.setItem(
                challenges.selectedKey,
                JSON.stringify(challenges.selectedItems)
            );
        },
        saveMapLayers() {
            localStorage.setItem(
                'selectedMapLayers',
                JSON.stringify(this.selectedMapLayers)
            );
            localStorage.setItem(
                'showLayeredMap',
                JSON.stringify(this.showLayeredMap)
            );
        },
        isSelectedMapLayer(value) {
            return this.selectedMapLayers.includes(value);
        },
        loadMapLayers() {
            const item = localStorage.getItem('selectedMapLayers');
            if (item) {
                try {
                    this.selectedMapLayers = JSON.parse(item);
                } catch (e) {
                    localStorage.removeItem('selectedMapLayers');
                }
            } else {
                this.selectedMapLayers = this.mapLayers;
            }
        },
        toggleAll(checked: boolean) {
            const newItems = checked ? [...this.challengeList.allItems] : [];
            Vue.set(this.challengeList, 'selectedItems', newItems);
        },
        showMapModal(mapname: string) {
            this.getModal('mapModal').show();
            this.mapName = mapname;
            switch (mapname) {
                case 'TheSkeld':
                    this.mapWireOrder =
                        'Electrical > Storage > Admin > Navigation > Cafeteria > Security';
                    this.locations = this.mapData.TheSkeld;
                    break;
                case 'MiraHQ':
                    this.mapWireOrder =
                        'Storage > Hallway > Locker Room > Greenhouse > Laboratory';
                    this.locations = this.mapData.MiraHQ;
                    break;
                case 'Polus':
                    this.mapWireOrder =
                        'Electrical > O2 > Office > Decontamination > Laboratory (right) > Laboratory (left/Bath)';
                    this.locations = this.mapData.Polus;
                    break;
                case 'Airship':
                    this.mapWireOrder =
                        'Viewing Deck > Engine Room > Main Hall > Showers > Lounge > Cargo Bay > Meeting Room';
                    this.locations = this.mapData.Airship;
                    break;
                default:
                    this.mapWireOrder = '';
            }
        },
        showChallengeListModal(challengeType: string) {
            switch (challengeType) {
                case 'General':
                    this.loadChallengeModal(
                        challengeType,
                        this.generalChallenges
                    );
                    break;
                case 'Crewmate':
                    this.loadChallengeModal(
                        challengeType,
                        this.crewmateChallenges
                    );
                    break;
                case 'Impostor':
                    this.loadChallengeModal(
                        challengeType,
                        this.impostorChallenges
                    );
                    break;
                case 'Lobby':
                    this.loadChallengeModal(challengeType, this.gameChallenges);
                    break;
            }
        },
        loadChallengeModal(
            challengeType: string,
            challenges: SelectableList<string>
        ) {
            const modal = this.getModal('challengeListModal');
            this.challengeType = challengeType;
            modal.title =
                challengeType +
                ' Challenges (' +
                challenges.selectedItems.length +
                ' / ' +
                challenges.allItems.length +
                ')';
            this.challengeListModalColor = 'dark';
            this.challengeList.allItems = challenges.allItems;
            this.challengeList.selectedItems = challenges.selectedItems;
            modal.show();
        },
    },
    mounted: function () {
        this.getData();
    },
    computed: {
        allSelected(): boolean {
            return (
                this.challengeList.allItems.length ===
                this.challengeList.selectedItems.length
            );
        },
    },
    watch: {
        selectedMapLayers() {
            this.saveMapLayers();
        },
        showLayeredMap() {
            this.saveMapLayers();
        },
    },
});

class SelectableList<T> {
    public allItems: T[] = [];
    public selectedItems: T[] = [];
    public currentItem?: T;

    constructor(public selectedKey: string) {}
}
